import React, { useEffect, useRef } from 'react'
import styled from 'styled-components'
import sky from '../images/sky.jpg'
import clouds1 from '../images/clouds1.png'
import clouds2 from '../images/clouds2.png'
import clouds3 from '../images/clouds3.png'
import clouds4 from '../images/clouds4.png'
import clouds5 from '../images/clouds5.png'
import grass from '../images/grass.png'
import cliff from '../images/cliff.png'
import sun from '../images/sun2.png'
import pic1 from '../images/pic1.jpg'
import pic2 from '../images/pic2.jpg'
import pic3 from '../images/pic3.jpg'
import pic4 from '../images/pic4.jpg'

export default function GallerySection() {
    const grassRef = useRef()
    const cliffRef = useRef()
    const sunRef = useRef()
    const knife1 = useRef()
    const knife2 = useRef()
    const knife3 = useRef()
    const knife4 = useRef()
    const text1 = useRef()
    const text2 = useRef()
    const text3 = useRef()
    const text4 = useRef()
    
    useEffect(() => {
        function handleScroll() {
            if (window.innerWidth > 900) {
                if (cliffRef.current.getBoundingClientRect().y < window.innerHeight &&
                    cliffRef.current.getBoundingClientRect().y > -500) {
                    cliffRef.current.style.bottom = `${window.scrollY / 1.5 - 4000}px`
                }
                if (sunRef.current.getBoundingClientRect().y < window.innerHeight) {
                    sunRef.current.style.top = `${window.scrollY + 7500}px`
                    sunRef.current.style.left = `${(window.scrollY * 3.5)}px`
                }
            }
            if (knife1.current.getBoundingClientRect().y < window.innerHeight / 1.2) {
                knife1.current.style.clipPath = 'polygon(0 0, 100% 0, 100% 100%, 0 100%)'
            }
            if (knife2.current.getBoundingClientRect().y < window.innerHeight / 1.2) {
                knife2.current.style.clipPath = 'polygon(0 0, 100% 0, 100% 100%, 0 100%)'
            }
            if (knife3.current.getBoundingClientRect().y < window.innerHeight / 1.2) {
                knife3.current.style.clipPath = 'polygon(0 0, 100% 0, 100% 100%, 0 100%)'
            }
            if (knife4.current.getBoundingClientRect().y < window.innerHeight / 1.2) {
                knife4.current.style.clipPath = 'polygon(0 0, 100% 0, 100% 100%, 0 100%)'
            }
            if (text1.current.getBoundingClientRect().y < window.innerHeight / 1.2) {
                text1.current.style.clipPath = 'polygon(0 0, 100% 0, 100% 100%, 0 100%)'
            }
            if (text2.current.getBoundingClientRect().y < window.innerHeight / 1.2) {
                text2.current.style.clipPath = 'polygon(0 0, 100% 0, 100% 100%, 0 100%)'
            }
            if (text3.current.getBoundingClientRect().y < window.innerHeight / 1.2) {
                text3.current.style.clipPath = 'polygon(0 0, 100% 0, 100% 100%, 0 100%)'
            }
            if (text4.current.getBoundingClientRect().y < window.innerHeight / 1.2) {
                text4.current.style.clipPath = 'polygon(0 0, 100% 0, 100% 100%, 0 100%)'
            }
        }

        window.addEventListener('scroll', handleScroll)

        return () => window.removeEventListener('scroll', handleScroll)

    }, [])



    return (
        <Container>
            <Wrapper className="wrapper">
                <img ref={knife1} src={pic1} alt="" />
                <p ref={text1}>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Culpa, tenetur iure. Aliquid provident ipsum esse rem alias deserunt vitae magnam veniam quaerat porro sint explicabo fuga sequi, fugiat, corrupti sapiente.</p>
                <p ref={text2}>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Culpa, tenetur iure. Aliquid provident ipsum esse rem alias deserunt vitae magnam veniam quaerat porro sint explicabo fuga sequi, fugiat, corrupti sapiente.</p>
                <img ref={knife2} src={pic2} alt=""/>
                <img ref={knife3} src={pic3} alt=""/>
                <p ref={text3}>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Culpa, tenetur iure. Aliquid provident ipsum esse rem alias deserunt vitae magnam veniam quaerat porro sint explicabo fuga sequi, fugiat, corrupti sapiente.</p>
                <p ref={text4}>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Culpa, tenetur iure. Aliquid provident ipsum esse rem alias deserunt vitae magnam veniam quaerat porro sint explicabo fuga sequi, fugiat, corrupti sapiente.</p>
                <img ref={knife4} src={pic4} alt=""/>
            </Wrapper>

            <Clouds3 src={clouds3} alt=""/>
            <Clouds1 src={clouds1} alt=""/>
            <Clouds2 src={clouds2} alt=""/>
            <Clouds4 src={clouds4} alt=""/>
            <Clouds5 src={clouds5} alt=""/>
            <Grass ref={grassRef} src={grass} alt=""/>
            <Cliff ref={cliffRef} src={cliff} alt=""/>
            <Sun ref={sunRef} src={sun} alt=""/>
        </Container>
    )
}




const Container = styled.section`
    height: 300vh;
    position: relative;
    overflow: hidden;
    background: url(${sky}) no-repeat top center/cover;
    clip-path: polygon(20% 0, 100% 0, 100% 100%, 0 100%, 0 10%);
    > img {
        position: absolute;
        transition: all 0.5s linear;
        left: -50vw;
        @keyframes cloud {
            to {
                left: 100vw;
            }
        }
    }
`
const Wrapper = styled.div`
    height: 100%;
    padding: 10vw 5vw 30vw;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    place-items: center;
    gap: 10vw;
    font-size: clamp(1rem, 3vw, 2rem);
    @media screen and (max-width: 900px) {
        grid-template-columns: 1fr;
    }
    >img {
        opacity: 0.8;
        border-radius: 10px;
        width: 70%;
        max-width: 70vw;
        transition: all 0.5s linear;
    }
    >img:nth-child(odd){
        clip-path: polygon(0 0, 0 0, 0 100%, 0 100%);
    }
    >img:nth-child(even) {
        clip-path: polygon(100% 0, 100% 0, 100% 100%, 100% 100%);
    }
    >p {
        text-align: justify;
        transition: all 0.5s linear;
        clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
        font-weight: 500;
        background-image: linear-gradient(45deg, var(--c1), #2c2c2c);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
`

const Clouds1 = styled.img`
    width: 50vw;
    top: 50vh;
    animation: cloud 100s infinite forwards linear -18s; 
`
const Clouds2 = styled.img`
    width: 20vw;
    top: 100vh;
    animation: cloud 110s infinite forwards linear; 
`
const Clouds3 = styled.img`
    width: 40vw;
    top: 0;
    animation: cloud 120s infinite forwards linear -5s; 
`
const Clouds4 = styled.img`
    width: 20vw;
    top: 150vh;
    animation: cloud 140s infinite forwards linear -10s; 
`
const Clouds5 = styled.img`
    width: 30vw;
    top: 100vh;
    animation: cloud 100s infinite forwards linear -15s; 
`
const Grass = styled.img`
    left: 0 !important;
    bottom: 0;
    width: 100vw;
    z-index: 10;
    transition: all 0.5s linear;
`
const Cliff = styled.img`
    left: 0 !important;
    bottom: 0;
    width: 100vw;
    z-index: 9;
`
const Sun = styled.img`
    top: 0;
    left: 0;
    z-index: -1;
    width: 200px;
    max-width: 20vw;
    @media screen and (max-width: 900px) {
        top: 80% !important;
        left: 70% !important;
    }
`
